import React, { 
  Component
} from 'react';

import {
  View,
}
from 'react-native';

// this is the extension to parse XML
var DomParser = require('react-native-html-parser').DOMParser

export default class ColorPatch extends Component {

  _renderRectangles(rectangles) {
    let views = [];

    for (var i = 0; i < rectangles.length; i++) {
      // get the rectangle
      let rectangle = rectangles[i];

      // first parse the colors
      let rgbColor = rectangle.getElementsByTagName('color')[0];
      let r = rgbColor.getAttribute('red')
      let g = rgbColor.getAttribute('green')
      let b = rgbColor.getAttribute('blue')

      // second the position
      let geometry = rectangle.getElementsByTagName('geometry')[0];
      let originX = parseFloat(geometry.getAttribute('x'))  * this.props.screenWidth
      let originY = parseFloat(geometry.getAttribute('y'))  * this.props.screenHeight
      let sizeX   = parseFloat(geometry.getAttribute('cx')) * this.props.screenWidth
      let sizeY   = parseFloat(geometry.getAttribute('cy')) * this.props.screenHeight

      views.push(<View 
        style={
          {
            position: 'absolute',
            marginLeft: originX,
            marginTop: originY,
            width: sizeX,
            height: sizeY,
            backgroundColor: 'rgb(' + r + ',' + g + ',' + b + ')'
          }
        }
        key={
          'rectangle-' + i
        }
      />)
    }
    return views;
  }

  render() {
    let doc = new DomParser().parseFromString(this.props.xml, 'text/xml')
    let rectangles = (doc != null) ? doc.getElementsByTagName('rectangle') : []

    return (
      <View style={{flex: 1, backgroundColor: 'black'}}>
        { this._renderRectangles(rectangles) }
      </View>
    );
  }
};


