import React from 'react'
import {
  AppRegistry,
  View
} from 'react-native'

import ColorPatch from './components/patch';

// const testXML = '<?xml version="1.0" encoding="UTF-8" ?><calibration><shapes><rectangle><color red="250" green="245" blue="0" /><geometry x="0.2" y="0.2" cx="0.6" cy="0.6" /></rectangle></shapes></calibration>';

const PROTOCOL = "urn:x-cast:com.caplaz.lightspaceconnect.patches";
const APPLICATION_TITLE = "LightSpace Connect";

class App extends React.Component {

  receiver = null
  messageBus = null

  constructor(props) {
    super(props);

    // set the log level
    cast.receiver.logger.setLevelValue(0);

    // set the initial state
    this.state = {
      messageStr: ' ' // testXML
    }
  }

  componentDidMount() {
    // init receiver
    this.receiver = cast.receiver.CastReceiverManager.getInstance();

    // handler for the 'ready' event
    this.receiver.onReady = function(event) {
      console.log('Received Ready event: ' + JSON.stringify(event.data));
      this.receiver.setApplicationState(APPLICATION_TITLE + " is ready...");
    }.bind(this);

    // handler for 'senderconnected' event
    this.receiver.onSenderConnected = function(event) {
      console.log('Received Sender Connected event: ' + event.data);
      console.log(this.receiver.getSender(event.data).userAgent);
    }.bind(this);

    // handler for 'senderdisconnected' event
    this.receiver.onSenderDisconnected = function(event) {
      console.log('Received Sender Disconnected event: ' + event.data);
      if (this.receiver.getSenders().length === 0) {
        window.close();
      }
    }.bind(this);

    // create a CastMessageBus to handle messages for a custom namespace
    this.messageBus = this.receiver.getCastMessageBus(PROTOCOL);

    // handler for the CastMessageBus message event
    this.messageBus.onMessage = function(event) {

      // pass the payload to the render
      this.setState({
        messageStr: event.data
      });

      // sender message listener will be invoked
      // this.messageBus.send(event.senderId, "Patch received");
    }.bind(this);

    // initialize the CastReceiverManager with an application status message
    this.receiver.start({
      statusText: APPLICATION_TITLE + " is starting"
    });

    console.log('Receiver Manager started');
  }

  componentWillUnmount() {
    this.receiver = null;
    this.messageBus = null;
  }
  
  render() {
    return (
      <View style={{flex: 1}}>
        <ColorPatch 
          style={{flex: 1}}
          xml={this.state.messageStr} 
          screenWidth={1280} 
          screenHeight={720} 
        />
      </View>
    );
  }
}

// App registration and rendering
AppRegistry.registerComponent('MyApp', () => App)
AppRegistry.runApplication('MyApp', {
  rootTag: document.getElementById('root')
})



